import React, { Component } from 'react';
import start_icon from "./static/img/play.svg"
import check_icon from "./static/img/check_blank.svg"
import checked_icon from "./static/img/check_full.svg"
import pause_icon from "./static/img/pause.svg"
import delete_icon from "./static/img/error.svg"
import postpone_icon from "./static/img/later.svg"
import link_icon from "./static/img/link.svg"

export default class Task extends Component{
  constructor(props){
    super(props);
    this.navigate = this.navigate.bind(this);
  }
  navigate(url){
    window.open(url,'_blank');
    this.props.onStart();
  }
  render(){
    const active = this.props.started;
    const complete = this.props.done;
    const edit = this.props.edit;
    var shallow = false;
    var deep = false;
    var link = false;
    var link_url = "";
    const tags = this.props.tags;
    const len = tags.length;
        for(var j = 0; j < len; j++){
            const t = tags[j];
            if(t.name === "shallow"){
                shallow = true;
            }
            if(t.name === "deep"){
                deep = true;
            }
            if(t.name.startsWith("link_")){
                link = true;
                link_url = t.name.substring(5);
            }
        }
      let desc = this.props.name;
      
      if(complete){
        desc=<s>{desc}</s>
      }
      if(shallow){
        desc=<i>{desc}</i>
      }
      if(deep){
        desc=<b>{desc}</b>
      }

    return (
        <div className="oneTask">
        <img className="leftButton" 
            src={((edit) ? delete_icon : (active ? pause_icon: (link ? link_icon : start_icon)))} 
            alt={((edit) ? "Delete" : ((active) ? "Pause" : "Start"))}
            onClick={edit ? this.props.onDelete : ((link && !active) ? () => this.navigate(link_url) : this.props.onStart)}
        />
        <span onClick={this.props.taskSelected}>
        {desc}
        </span>
        <img className="rightButton" 
        src={(edit ? postpone_icon : (complete ? checked_icon : check_icon))} 
        alt={(edit ? "Postpone" : (complete ? "Uncomplete" : "Complete"))}
        onClick={edit ? this.props.onPostpone : this.props.onComplete} 
        />
        </div>
    );
  }
}
